/* Typography*/
@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@500&family=Merriweather:wght@700&display=swap");
/* alerts  onloading animations */
@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}
.loading {
  width: 6rem;
  height: 6rem;
  margin: 0 auto;
  border: 5px solid #94a3b8;
  border-radius: 50%;
  border-top-color: #645cff;
  animation: spinner 0.6s linear infinite;
}

html {
  font-size: 100%;
}

/*16px*/
body {
  display: grid;
  place-items: center;
  background: #e4e6f6;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 400;
  line-height: 1.75;
  color: #151a41;
  width: 100vw;
  height: 100vh;
}

p {
  max-width: 40em;
  margin-bottom: 1.5rem;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  margin-bottom: 1.38rem;
  font-family: "Merriweather", serif;
  font-weight: 400;
  line-height: 1.3;
  text-transform: capitalize;
}

h1 {
  margin-top: 0;
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
}

h4 {
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

small,
.text-small {
  font-size: 0.9rem;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
  padding: 0;
}

/* buttons */
.btn {
  cursor: pointer;
  color: white;
  background: #645cff;
  border: transparent;
  border-radius: 0.25rem;
  letter-spacing: 1px;
  padding: 0.375rem 0.75rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  transition: 0.3s ease-in-out all;
  text-transform: capitalize;
  display: inline-block;
  /*block class is good with forms*/
}
.btn:hover {
  background: #3c3799;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.btn-hipster {
  color: #645cff;
  background: #c0bdff;
}
.btn-hipster:hover {
  color: #c0bdff;
  background: #3c3799;
}
.btn-block {
  width: 100%;
}

/* alerts */
.alert {
  padding: 0.375rem 0.75rem;
  margin-bottom: 1rem;
  border-color: transparent;
  border-radius: 0.25rem;
}

.alert-errors {
  color: #842029;
  background: #f8d7da;
}

.alert-success {
  color: #0f5132;
  background: #d1e7dd;
}

/* forms */
.form {
  width: 90vw;
  max-width: 600px;
  background: white;
  border-radius: 0.25rem;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  padding: 2rem 2.5rem;
  margin: 3rem auto;
}

.form-label {
  display: block;
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
  text-transform: capitalize;
  letter-spacing: 1px;
}

.form-input,
.form-textarea {
  width: 100%;
  padding: 0.375rem 0.75rem;
  border-radius: 0.25rem;
  background: #f8fafc;
  border: 1px solid #e2e8f0;
}

.form-control {
  margin-bottom: 1rem;
}

.form-textarea {
  height: 7rem;
}

::placeholder {
  font-family: inherit;
  color: #94a3b8;
}

.form-alert {
  color: #842029;
  letter-spacing: 1px;
  text-transform: capitalize;
}

/* titles */
.title {
  text-align: center;
}

.title-underline {
  background: #151a41;
  width: 7rem;
  height: 0.25rem;
  margin: 0 auto;
  margin-top: -1rem;
}

/* images */
.imgContainer {
  width: 40vw;
  max-width: 1120px;
  border-radius: 0.25rem;
}
.imgContainer .img {
  width: 100%;
  display: block;
  object-fit: cover;
}

.card {
  width: 100%;
  max-width: 500px;
  min-height: 500px;
  padding: 1.8rem 2.5rem;
  border-radius: 2rem;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  background-color: white;
  display: grid;
  place-items: center;
}
.card__body {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  padding: 0.5rem 1rem;
}
.card__body svg {
  position: absolute;
  top: -40px;
  right: 10px;
  color: #e4e6f6;
  transition: 0.7s ease-in-out all;
}
.card__body svg:hover {
  color: #3743a4;
}
.card__body h2 {
  font-size: 18px;
  font-weight: 700;
  line-height: 28px;
  color: #151a41;
  text-transform: initial;
}
.card__body__count {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.card__body__count span {
  width: 63px;
  height: 53px;
  display: grid;
  place-items: center;
  border: 1px solid #e4e6f6;
  color: #151a41;
}
.card__body__count span.active {
  color: #e4e6f6;
  background-color: #151a41;
}
.card__body__gauge {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.card__body__checkbox {
  align-self: start;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
.card__body__checkbox .input,
.card__body__checkbox .label {
  font-size: 1rem;
  line-height: 28px;
}
.card__body__hr {
  content: "";
  width: 100%;
  height: 2pxs;
  background-color: #e4e6f6;
}
.card__body button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  outline: none;
  border: 1px solid #e4e6f6;
  background-color: #fff;
  color: #151a41;
  position: relative;
  padding: 1rem 2rem;
  transition: 0.7s ease-in-out all;
}
.card__body button svg {
  color: #151a41;
  top: 35%;
  right: 35%;
  transition: 0.7s ease-in-out all;
}
.card__body button:hover {
  transform: scale(0.98);
  background-color: #151a41;
  color: #e4e6f6;
}
.card__body button:hover svg {
  color: #e4e6f6;
}

*,
::after,
::before {
  box-sizing: border-box;
}

.container {
  max-width: 90%;
  display: grid;
  place-items: center;
  gap: 2rem;
}